.main{
	display:flex;
	justify-content: space-around;
	flex-wrap: wrap;
	
}
body{
	background-color: #d2dbdd;
	margin: 0;
}
.nav{
	text-align: center;
	background-color: #fff;
	color:#868686;
	font-size: 2rem;
	padding: 10px;
	margin: 0px;
	font-weight: 500;
	letter-spacing: 2px;
	margin-bottom: 30px;
	font-family: "Raleway",sans-serif;
}

.card{
	background-color: lightseagreen;
	
	width: 25%;
	padding: 10px;
	margin: 30px;
	box-shadow: 0px 0px 19px black;

}
.card:hover{
	font-size: 150%;
	background-color: darkgoldenrod;
	transition: .5s;
	transform: scale(1.1);
	
}
.card_info{
	text-align: center;
	font-size: 16px;
	font-weight: bold;
	font-family: cursive;
}
.card_info button{
	text-decoration: none;
	color: white;
	background-color: black;
	box-shadow: 0px 0px 5px yellow;
	padding: 6px;
	margin: 15px;
}
.card_info button:hover{
	background-color: darkgreen;
	transition: .3s;
}
img{
	width: 100%;
	height: 300px;
}
@media(max-width:598px){
	.card{
		display: block;
		width: 300px;
	}
	.main{
		display: flex;
		justify-content: center;
		
		
	}
}